.header-color{
background: rgb(131,58,180);
background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);
}
.Landing-grid {
  background: rgb(131,58,180);
background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);
text-align: center;
/* position: fixed; */
top:0;
left:0;
width: 100%;
height: 100%;
}
.avatar-img {
  height: 250px;
  padding-top:5em;
}
.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin:auto;
  border-radius:10px;
  -webkit-border-radius:10px;
  -moz-border-radius:10px;
  -ms-border-radius:10px;
  -o-border-radius:10px;
}
.banner-text h1{
font-size: 66px;
font-weight: bold;
color:white;
}
.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}
.banner-text p{
  color:White;
  font-size:20px;
  padding:1em;
}

.social-links {
display: flex;
justify-content: space-between;
width: 50%;
margin: auto;
}

.social-links i{
  color:white;
  font-size: 5em;
  padding: 8px;
}

.projectsreact-grid{
  display:flex;

}

.projectsspring-grid{
  display:flex;

}
.contact-body{
  margin:auto;
  /* position:fixed; */
  width:100%;
  height:100%;
  justify-content: center;
  padding-top: 2em;
}
.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  /* border: 2px solid red; */
  margin-bottom: 20px;
}

.contact-grid h2 {
font-family: 'Anton', sans-serif;
}
.contact-grid p{
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}
.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}
.contact-list i {
  font-size: 66px;
  padding-right: 5rem;
}

/* Resume page css */
.resume-right-col {
  background: black;
  color:white;
  padding: 5em;
}

.About-grid {
  background: rgb(131,58,180);
background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(29,253,213,1) 50%, rgba(252,176,69,1) 100%);
text-align: center;
/* position: fixed; */
top:0;
left:0;
width: 100%;
height: 100%;
}
.abouttext{
  font-size: 22px;
}